import constants from "./constants";

function $(selector:string) {
  return document.querySelector(selector) as HTMLElement;
}

const $stage = $('.stage');

function init() {
  $stage.style.width = constants.stageWidth + "px";
  $stage.style.height = constants.stageHeight + "px";
  drawGrid(1, 1);
}

// let square = symbolSquare.draw(stage, 100)
// console.log(stage)


function drawGrid(x: number, y: number) {
  const gridNode = document.createElement('svg')
  const gridRect = document.createElement('rect')
  $stage.appendChild(gridNode);
  gridRect.setAttribute('height', '10')
  gridRect.setAttribute('width', '10')
  gridNode.appendChild(gridRect)
}


